/*----------------------------------------------------*
    CUSTOM FORM ( APP INPUT ( TEXT, DATE, NUMBER ) )
 *----------------------------------------------------*/

.row {
    margin-left: 0px;
    margin-right: 0px;
}

.text-label-green {
    color: #3d6614 !important;
    font-weight: bolder;
    font-size: 0.875rem
}

.btn {
    font-size: 0.875rem;
    font-weight: bolder;
}

.btn.btn-success {
    min-width: 80px;
}

.container-fluid-custom {
    padding-bottom: 8px;
    padding-top: 4px;
}


/* Info area, not include button area height */

.info-container {
    height: calc(100% - 45px);
    overflow-x: hidden;
    overflow-y: scroll;
    padding-top: 5px;
}

.button-container {
    padding-top: 10px;
    padding-left: 12px;
    padding-right: 12px;
    display: flex;
}

.button-box {
    flex: 1 1 100%;
}

.button-box.left {
    text-align: left;
    padding-right: 10px;
}

.button-box.center {
    text-align: center;
}

.button-box.right {
    text-align: right;
    padding-left: 10px;
}

.app-input-group {
    position: relative;
}

.app-input-icon {
    position: absolute;
    top: 3px;
    right: 8px;
}

i.fa.fa-calendar {
    font-size: 14px;
}

.app-input-icon.calendar {
    position: absolute;
    top: 3px;
    right: 41px;
    z-index: 9;
}

.app-input-icon>i {
    font-size: 1.4rem;
    color: #3d6614;
}

.app-input-icon:hover {
    cursor: pointer;
}

.app-input-field {
    padding-right: 45px;
}

.menu-item {
    background-color: #3b75c9;
    padding: 15px;
    border-radius: 0.25rem;
    margin-bottom: 7px;
}

.menu-item-text {
    color: white;
}

.aer {
    text-align: center;
    padding: 7px !important;
}

.aer-f-type {
    background-color: #1f4e78;
    font-weight: bold;
}

.aer-k-type {
    background-color: #2f75b5;
    font-weight: bold;
}

.aer-h-type {
    background-color: #70ad47;
    font-weight: bold;
}

.aer-g-type {
    background-color: #f6f;
    font-weight: bold;
}

.aer-s-type {
    background-color: #fff;
    font-weight: bold;
    border: 1px solid black;
}

.aer-s-type>span {
    color: black !important;
    font-weight: bold;
}

.menu-item.aer-list-ec {
    border: 1px solid #3d6614;
    border-radius: 0.2rem;
    padding: 7px !important;
    background: #dddddd92;
}

.menu-item.aer-list-ec .menu-item-text {
    color: #3d6614;
    font-weight: bold;
}

.aer-border-cst {
    border: 1px solid #3d6614;
    border-radius: 0.2rem;
    padding: 12px 0px;
    margin-top: 5px
}

.aer-btn-cst {
    border: 1px solid #3d6614;
    background: #dddddd92;
    border-radius: 0.2rem;
    width: 100%;
}

.aer-custom-padding {
    padding-left: 0px;
    padding-right: 0px;
}

.text-wrap {
    word-break: break-all !important;
    text-decoration: underline;
}

.header {
    height: 40px;
    border-bottom: 2px solid #3d7403;
}

.main {
    height: calc(100% - 75px);
}

.footer {
    margin-top: auto;
    font-size: 0.7rem;
    background: #3f730b;
    border-top: 2px solid #91be62;
    height: 35px
}

.nav-custom {
    flex: 1 1 100%;
    text-align: center;
}

.nav-custom.left {
    flex: 1 1 23%;
    text-align: center;
}

.nav-custom.center {
    flex: 1 1 54%;
    text-align: center;
}

.nav-custom.right {
    flex: 1 1 23%;
    text-align: center;
}

.full-screen {
    font-size: 0.825px;
}

.menu-sm img {
    width: 140px;
    padding-right: 5px;
    content: url(assets/images/img_logo_header.png);
}

.menu-sm li {
    padding: 10px;
    font-weight: bold;
    color: #3f730b;
}

.menu-sm li:last-child {
    padding: 0px 0px 15px 10px;
}

.btn-outline-success:hover {
    color: #3d7403;
    background-color: white;
    border-color: #3d7403;
}

.btn-outline-success.dropdown-toggle.show {
    color: #3d7403;
    background-color: #fff;
    border-color: #3d7403;
}

.dropdown-menu.dropdown-custom-list {
    min-width: 20rem !important;
}

.menu-child-list-item {
    margin-left: -10px;
}

.menu-child-list-item-text {
    font-weight: bold;
    color: #3d6614;
    font-size: 20px;
}

.menu-child-list-item:hover .menu-child-list-item-text {
    cursor: pointer;
    border-bottom: 1px solid #0d6efd;
}

.btn-circle {
    border-radius: 50%;
}

.modal-footer {
    justify-content: center !important;
}

.modal-header h1 {
    font-size: 16px;
}

.menu-header-text,
.modal-body h3 {
    font-size: 0.825rem;
}

.dropdown-toggle {
    padding: 0px;
}

.table-header-area {
    /* overflow: hidden; */
}

.table-body-area {
    overflow: auto;
    border-bottom: 1px solid #3f730b;
}

.table-bordered-custom,
.table-check-custom {
    border-collapse: separate;
    border-spacing: 0;
    border-width: thin;
    width: 100%;
    table-layout: fixed;
    font-size: 20px;
}

.table-bordered-custom thead tr th {
    background-color: #3f730b;
    color: white;
}

.table-bordered-custom thead th {
    border-right: 1px solid white;
    border-top: 1px solid white;
}

.table-bordered-custom thead tr:first-child th:last-child {
    border-right: 1px solid #3f730b !important;
}

.table-bordered-custom thead tr:first-child th {
    border-top: 1px solid #3f730b !important;
}

.table-bordered-custom thead tr:last-child th,
.table-bordered-custom thead tr:first-child th[rowspan] {
    border-bottom: 1px solid #3f730b;
}

.table-bordered-custom thead tr:first-child th:first-child {
    border-left: 1px solid #3f730b;
}

.table-bordered-custom tbody td {
    border-top: 1px solid #3d6614;
    border-right: 1px solid #3d6614;
}

.table-bordered-custom tbody tr:last-child td {
    border-top: 1px solid #3d6614;
}


/* .table-bordered-custom tbody tr:last-child td,
.table-bordered-custom tbody tr:first-child td[rowspan] {
  border-bottom: 1px solid #3d6614;
} */

.table-bordered-custom tbody tr:first-child td:first-child {
    border-left: 1px solid #3d6614;
}

td,
th {
    height: 40px;
    padding-left: 5px;
    padding-right: 5px;
}

.cst-fontsize-notify {
    font-size: 1.5rem!important;
}

@media (max-width: 414px) {
    .cst-fontsize-notify {
        font-size: 1.2rem!important;
    }
    .menu-sm img {
        width: 35px;
        content: url(assets/images/img_logo_header_small.png);
    }
    .menu-sm .dropdown-toggle {
        padding: 2px;
    }
    .dropdown-menu.dropdown-custom-list {
        min-width: 16rem !important;
    }
    img {
        max-height: 130px;
    }
}

@media (max-height: 300px) {
    .header {
        opacity: 0.1;
    }
    .info-container {
        height: 100%;
    }
    .button-container {
        height: 0px;
        display: none;
    }
    .footer {
        height: 0px;
        display: none !important;
    }
}

@media (max-height: 800px) {
    .input-group-sm .dropdown-menu.show {
        transform: translate(0px, -103px) !important;
    }
}

.mark-option {
    color: red!important;
}

.border-img-cst {
    border: 1px solid #3f730b;
    border-radius: 5px;
    text-align: center;
}